import ENVIRONMENT_CONFIG from '../../config/envConfig.js'
import {mapState} from 'vuex'
import util from '@/utils/util.js'
export default {
    onLoad(par) {
        this.positionId = par.positionId
        this.positionName = par.positionName
        this.otherToken = par.token
        this.salary = par.salary
        uni.request({
            url: ENVIRONMENT_CONFIG.host+'/tr/api/talentRecommend/recommendUserInfo?'+ENVIRONMENT_CONFIG.code,
            method: 'POST',
            header:{
                'Authorization': 'Bearer '+this.token
            },
            success: res => {
                let realRes = res.data
                if(realRes.code==0){
                    this.name = realRes.data.recommendUserName
                    this.company = realRes.data.recommendUserCompany
                    this.position = realRes.data.recommendUserPosition
                }else{
                    util.show(realRes.message)
                }
            },
            fail: () => {
                util.show('获取推荐信息失败')
            }
        })
    },
    computed: {
        ...mapState(['token','userId'])
    },
    data() {
        return {
            name: '',
            company: '',
            position: '',
            positionId: '',
            positionName: '',
            otherToken: '',
            salary: ''
        }
    },
    onShareAppMessage: function () {
        uni.request({
            url: ENVIRONMENT_CONFIG.host+'/tr/api/talentRecommend/updateRecommendUserInfo?'+ENVIRONMENT_CONFIG.code,
            method: 'POST',
            header:{
                'Authorization': 'Bearer '+this.token
            },
            data: {
                recommendUserName: this.name,
                recommendUserCompany: this.company,
                recommendUserPosition: this.position
            },
            success: res => {
                let realRes = res.data
                if(realRes.code!=0){
                    util.show(realRes.message)
                }else{
                }
            },
            fail: () => {
               util.show('更新推荐信息失败')
            }
        })
        return{
            title: '【热招】 '+this.positionName + ', 薪资：' + this.salary,
            path: '/pages/position/index?positionId='+this.positionId+'&token='+this.otherToken+'&recommendUserName='+this.name+'&recommendUserCompany='+this.company+'&recommendUserPosition='+this.position,
            imageUrl: '/static/img/share.png'
        }
    }
}
